import React, { useState } from "react";
import classes from "./Faq.module.css";

const Faq = () => {
  const [answer, showAnswer] = useState(false);

  const showAnswerHandler = () => {
    showAnswer((prev) => !prev);
  };

  return (
    <div className={classes.container}>
      <h2>Frequently Asked Questions</h2>

      <>
        <div className={classes.question} onClick={showAnswerHandler}>
          <div className={classes.arrow}>*</div>
          <p>What is IV Hydration?</p>
        </div>
        {answer && (
          <p className={classes.answer}>
            IV hydration is a simple treatment that delivers fluids directly
            through a small IV inserted into your arm
          </p>
        )}
        <div className={classes.border}></div>
      </>

      <>
        <div className={classes.question} onClick={showAnswerHandler}>
          <div className={classes.arrow}>*</div>
          <p>What is IV hydration?</p>
        </div>
        {answer && (
          <p className={classes.answer}>
            IV hydration is a simple treatment that delivers fluids directly
            through a small IV inserted into your arm
          </p>
        )}
        <div className={classes.border}></div>
      </>

    </div>
  );
};

export default Faq;
